.my--form {
    max-width: 400px;
    padding: 20px;
    border: 1px solid rgb(9, 9, 9);
    margin: 0 auto;
    box-shadow: 2px 2px 5px #070707;
  }
  
  .my--form label {
    display: block;
    margin-bottom: 10px;
    color:rgb(240, 6, 68);
    font-size:larger;
    font-weight: bold;
    -webkit-text-decoration: underline 3px;
            text-decoration: underline 3px;
    
  }
  
  .my--form input[type="text"],
  .my--form input[type="submit"] {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
    border: 2px solid rgb(5, 1, 1);
    border-radius: 4px;

    
  }
  
  .my--form input[type="submit"] {
    background-color: #02e85e;
    color: white;
    cursor: pointer;
  }
  
  .my--form input[type="submit"]:hover {
    background-color: #45a049;
  }
  .my-form-background{
    background-image: url('https://cdn.pixabay.com/photo/2013/07/18/20/26/sea-164989__340.jpg');
